import React, { Fragment,useEffect,useRef,useState } from 'react';
import { useDispatch,useSelector } from 'react-redux';
import { Link, useParams } from "react-router-dom";
import { clearErrors, getProductDetails, userReview } from '../../actions/productActions';
import { Carousel} from "react-bootstrap";
import MetaData from '../layout/MetaData';
import "bootstrap/dist/css/bootstrap.min.css";
import Loader from '../layout/Loader';
import { toast } from 'react-toastify';
import { addToCart } from '../../actions/cartActions';
import { NEW_REVIEW_RESET } from '../../constants/productConstants';
import ListOfReviews from '../reviews/ListOfReviews';

const ProductDetails = () => {
    const [quantity,setQuantity] = useState(1);
    const [rating,setRating] = useState(0);
    const [comment,setComment] = useState("");

    
    const params = useParams();
    const dispatch=useDispatch();
    const count = useRef();

    const { product,loading,error } = useSelector(state=>state.productDetails);
    const { user } = useSelector(state => state.auth);
    const { error:reviewError,success } = useSelector(state => state.newReview);
    
    useEffect(()=>{
        dispatch(getProductDetails(params.id));

        if(error){
            toast.error(error);
            dispatch(clearErrors);
        }

        if(reviewError){
            toast.error(reviewError);
            dispatch(clearErrors());
        }

        if(success) {
            toast.success("Review Posted successfully");
            dispatch({
                type:NEW_REVIEW_RESET
            });
        }
    },[dispatch,params.id,error,success,reviewError]);

    const handleAddToCart=()=>{
        dispatch(addToCart(params.id,quantity));
        toast.success("PRODUCT ADDED TO CART SUCCESSFULLY");
    }

    const handleIncreaseQty=()=>{
        if(count.current.valueAsNumber>=product.stock) return;

        count.current.valueAsNumber = count.current.valueAsNumber + 1;

        setQuantity(count.current.valueAsNumber);
    }
    const handleDecreaseQty=()=>{
        if(count.current.valueAsNumber<=1) return;

        count.current.valueAsNumber = count.current.valueAsNumber - 1;

        setQuantity(count.current.valueAsNumber);
    }
    const setUserRatings = ()=>{
        const stars = document.querySelectorAll(".star");
        stars.forEach((star,index)=>{
            star.starValue = index +1;
            
            ["click","mouseover","mouseout"].forEach(function(event){
                star.addEventListener(event,showRatings);
            })
        });

        function showRatings(e){
            stars.forEach((star,index)=>{
                if(e.type==="click"){
                    if(index < this.starValue) {
                        star.classList.add("orange");
                        setRating(this.starValue);
                    }else{
                        star.classList.remove("orange");
                    }
                }
                if(e.type==="mouseover"){
                    if(index < this.starValue){
                        star.classList.add("yellow");
                    }else {
                        star.classList.remove("yellow");
                    }
                }
                if(e.type === "mouseout"){
                    star.classList.remove("yellow");
                }
            })
        }
    }

    const handleSubmitReview = ()=>{
        const formData = new FormData();

        formData.set("rating",rating);
        formData.set("comment",comment);
        formData.set("productId",params.id);

        dispatch(userReview(formData));
    }
  return (
    <Fragment>
        <MetaData title="Product Details"/>
        {
            loading ? <Loader /> : (
        <Fragment>
            <div className="row f-flex justify-content-around">
                    <div className="col-12 col-lg-5 img-fluid" id="product_image">
                        <Carousel pause="hover" variant="dark">
                            {
                                product.images && product.images.map(image=>(
                                    <Carousel.Item key={image.public_id}>
                                        <img className="d-block w-100" src={image.url} alt={product.name} />
                                    </Carousel.Item>
                                ))
                            }
                        </Carousel>
                    </div>

                    <div className="col-12 col-lg-5 mt-5">
                        <h3>{product.name}</h3>
                        <p id="product_id">Product # {product._id}</p>

                        <hr />

                        <div className="rating-outer">
                            <div className="rating-inner" style={{width:`${(product.ratings/5)*100}%`}}></div>
                        </div>
                        <span id="no_of_reviews">({product.numberOfReviews} Reviews)</span>

                        <hr />

                        <p id="product_price">${product.price}</p>
                        <div className="stockCounter d-inline">
                            <span className="btn btn-danger minus" onClick={handleDecreaseQty}>-</span>

                            <input type="number" className="form-control count d-inline" value={quantity} ref={count} readOnly />

                            <span className="btn btn-primary plus" onClick={handleIncreaseQty}>+</span>
                        </div>
                        <button type="button" id="cart_btn" className="btn btn-primary d-inline ml-4" onClick={handleAddToCart} disabled={ !product.stock }>Add to Cart</button>

                        <hr />

                        <p>Status: <span id="stock_status" className={product.stock >0 ? "greenColor":"redColor"}>{product.stock>0 ? "In Stock" : "Out of Stock"}</span></p>

                        <hr />

                        <h4 className="mt-2">Description:</h4>
                        <p>{product.description}</p>
                        <hr />
                        <p id="product_seller mb-3">Sold by: <strong>{product.seller}</strong></p>
                        
                        {
                            user ?   <button id="review_btn" type="button" className="btn btn-primary mt-4" data-toggle="modal" data-target="#ratingModal" onClick={setUserRatings}>
                                        Submit Your Review
                                    </button>
                                :   <div className="alert alert-danger mt-5 text-center"><Link to="/login" className="text-danger">Login</Link> To Post Your Review</div>
                        }
                        
                        
                        <div className="row mt-2 mb-5">
                            <div className="rating w-50">

                                <div className="modal fade" id="ratingModal" tabIndex="-1" role="dialog" aria-labelledby="ratingModalLabel" aria-hidden="true">
                                    <div className="modal-dialog" role="document">
                                        <div className="modal-content">
                                            <div className="modal-header">
                                                <h5 className="modal-title" id="ratingModalLabel">Submit Review</h5>
                                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                                    <span aria-hidden="true">&times;</span>
                                                </button>
                                            </div>
                                            <div className="modal-body">

                                                <ul className="stars" >
                                                    <li className="star"><i className="fa fa-star"></i></li>
                                                    <li className="star"><i className="fa fa-star"></i></li>
                                                    <li className="star"><i className="fa fa-star"></i></li>
                                                    <li className="star"><i className="fa fa-star"></i></li>
                                                    <li className="star"><i className="fa fa-star"></i></li>
                                                </ul>

                                                <textarea 
                                                    name="review" 
                                                    id="review" 
                                                    className="form-control mt-3" 
                                                    onChange={(event)=>setComment(event.target.value)}
                                                    value={comment}>

                                                </textarea>

                                                <button 
                                                    className="btn my-3 float-right px-4 text-white"
                                                    style={{
                                                        backgroundColor: "#fa9c23",
                                                        borderColor: "#fa9c23",
                                                        marginTop: "2rem",
                                                        borderRadius: "5rem"
                                                    }}
                                                    data-dismiss="modal" 
                                                    aria-label="Close"
                                                    onClick={handleSubmitReview}>Submit</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                        </div>						
                    </div>
                </div>
            </div>
            {
                product.reviews && product.reviews.length > 0 && (
                    <ListOfReviews reviews={product.reviews} />
                )
            }
        </Fragment>
            )
        }
    </Fragment>
  )
}

export default ProductDetails
