import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useNavigate } from "react-router-dom";
import { addToCart, removeFromCart } from '../../actions/cartActions';

const Cart = () => {
    const { cartItems } = useSelector(state=>state.cart);

    const dispatch = useDispatch();
    const navigate = useNavigate();

    const handleRemoveFromCart=(productId)=>{
        dispatch(removeFromCart(productId));
    }

    const handleIncreaseQty = (id,quantity,stock)=>{

        if(quantity >= stock) return;
        
        const newQty = quantity + 1;
        dispatch(addToCart(id,newQty));
    }
    const handleDecreaseQty = (id,quantity)=>{

        if(quantity <= 1) return;
        
        const newQty = quantity - 1;
        dispatch(addToCart(id,newQty));
    }
    const handleCheckOut=()=>{
        navigate("/login?redirect=shipping");
    }
  return (
    <React.Fragment>
         <h2 className="mt-5">Your Cart: <b>{cartItems.length} items</b></h2>
        
        <div className="row d-flex justify-content-between">
            <div className="col-12 col-lg-8">
                {
                    cartItems.map(item =>(
                        <React.Fragment key={item.product}>
                            <hr />
                            <div className="cart-item">
                                <div className="row">
                                    <div className="col-4 col-lg-3">
                                        <img src={item.image} alt={item.name} height="90" width="115" />
                                    </div>

                                    <div className="col-5 col-lg-3">
                                        <Link to={`/product/${item.product}`}>{item.name}</Link>
                                    </div>


                                    <div className="col-4 col-lg-2 mt-4 mt-lg-0">
                                        <p id="card_item_price">${item.price}</p>
                                    </div>

                                    <div className="col-4 col-lg-3 mt-4 mt-lg-0">
                                        <div className="stockCounter d-inline">
                                            <span className="btn btn-danger minus" onClick={()=>handleDecreaseQty(item.product,item.quantity)}>-</span>
                                            <input type="number" className="form-control count d-inline" value={item.quantity} readOnly />

                                            <span className="btn btn-primary plus" onClick={()=>handleIncreaseQty(item.product,item.quantity,item.stock)}>+</span>
                                        </div>
                                    </div>

                                    <div className="col-4 col-lg-1 mt-4 mt-lg-0">
                                        <i id="delete_cart_item" className="fa fa-trash btn btn-danger" onClick={()=>handleRemoveFromCart(item.product)}></i>
                                    </div>

                                </div>
                            </div>
                        </React.Fragment>
                    ))
                }
            <hr />
            </div>

            <div className="col-12 col-lg-3 my-4">
                <div id="order_summary">
                    <h4>Order Summary</h4>
                    <hr />
                    <p>Subtotal:  <span className="order-summary-values">{cartItems.reduce((acc,item)=>acc+Number(item.quantity),0)} (Units)</span></p>
                    <p>Est. total: <span className="order-summary-values">${cartItems.reduce((acc,item)=>acc+(Number(item.quantity)*Number(item.price)),0).toFixed(2)}</span></p>
    
                    <hr />
                    <button id="checkout_btn" className="btn btn-primary btn-block" onClick={handleCheckOut} disabled={!cartItems.length}>Check out</button>
                </div>
            </div>
        </div>
    </React.Fragment>
  )
}

export default Cart
